.text-block {
    margin: rem-calc(70) 0;

    &__title {
        color: map-deep-get($colors, "blue", "default");
    }

    &__button {
        margin-top: rem-calc(20);
    }

    &__content {
        display: flex;
        align-items: center;
        align-content: center;
        height: 100%;
        flex-wrap: wrap;
    }
}